.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  text-align: center;
}
.col-md-6{
  margin-top: 100px;  
  height: 400px;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.form-group{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}
input{
  width: 200px;
}
button{
  background-color: aquamarine;
}
.header-wrapper {
  display: flex;
  justify-content: space-between;
}
.body-wrapper {
  display: flex;
  justify-content: space-between;
  margin-left: -15px;
}

.image-wrapper {
  width: 50%;
  margin-left: 15px;
}

.login-page-image {
  width: 100%;
  height: 85vh;
}

.register-right-up-corner {
  padding-right: 2rem;
}

.form-login-register {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  line-height: 22px;
  padding: 1rem 0.5rem 0.5rem 0.5rem;
}
